<template>
  <b-modal
    id="EditCat"
    cancel-variant="outline-secondary"
    ok-title="update"
    cancel-title="cancel"
    centered
    size="lg"
    title="Edit Employee"
    @ok="update"
  >
    <validation-observer ref="simpleRules">
      <b-form>
        <b-row>
          <!-- username -->
          <b-col cols="6">
            <b-form-group
              label="First Name"
              label-for="firstName"
            >
              <validation-provider
                #default="{ errors }"
                name="name"
                rules="required"
              >
                <b-form-input
                  id="name"
                  v-model="dataFirstName"
                  :state="errors.length > 0 ? false:null"
                  placeholder="first name"
                />
                <small class="text-danger">{{ errors[0] }}</small>
              </validation-provider>
            </b-form-group>
          </b-col>
          <!-- username -->
          <b-col cols="6">
            <b-form-group
              label="Last Name"
              label-for="lastName"
            >
              <validation-provider
                #default="{ errors }"
                name="lastName"
                rules="required"
              >
                <b-form-input
                  id="name"
                  v-model="dataLastName"
                  :state="errors.length > 0 ? false:null"
                  placeholder="last name"
                />
                <small class="text-danger">{{ errors[0] }}</small>
              </validation-provider>
            </b-form-group>
          </b-col>
          <!-- username -->
          <b-col cols="6">
            <b-form-group
              label="Email"
              label-for="email"
            >
              <validation-provider
                #default="{ errors }"
                name="email"
                rules="required"
              >
                <b-form-input
                  id="name"
                  v-model="dataEmail"
                  type="email"
                  :state="errors.length > 0 ? false:null"
                  placeholder="email"
                />
                <small class="text-danger">{{ errors[0] }}</small>
              </validation-provider>
            </b-form-group>
          </b-col>
          <!-- username -->
          <b-col cols="6">
            <b-form-group
              label="Phone"
              label-for="phone"
            >
              <validation-provider
                #default="{ errors }"
                name="phone"
                rules="required"
              >
                <b-form-input
                  id="phone"
                  v-model="dataPhone"
                  :state="errors.length > 0 ? false:null"
                  placeholder="phone"
                />
                <small class="text-danger">{{ errors[0] }}</small>
              </validation-provider>
            </b-form-group>
          </b-col>
          <!-- username -->
          <b-col cols="6">
            <b-form-group
              label="Started At"
              label-for="startedAt"
            >
              <validation-provider
                #default="{ errors }"
                name="startedAt"
                rules="required"
              >
                <b-form-input
                  id="name"
                  v-model="dataStartedAt"
                  type="date"
                  :state="errors.length > 0 ? false:null"
                  placeholder="started At"
                />
                <small class="text-danger">{{ errors[0] }}</small>
              </validation-provider>
            </b-form-group>
          </b-col>

          <b-col md="6">
            <b-form-group
              label="Company"
              label-for="company"
            >
              <validation-provider
                #default="{ errors }"
                name="type"
                rules="required"
              >
                <b-form-select
                  id="country"
                  v-model="companyId"
                  placeholder="Select Company"
                  value-field="id"
                  text-field="name"
                  :options="$store.state.company.companies"
                  :state="errors.length > 0 ? false : null"
                  @change="companyId"
                />

                <small class="text-danger">{{ errors[0] }}</small>
              </validation-provider>
            </b-form-group>
          </b-col>

        </b-row>
      </b-form>
    </validation-observer>
  </b-modal>
</template>

<script>
import {
  BCol,
  BForm, BFormGroup, BFormInput, BFormSelect, BModal, BRow,
} from 'bootstrap-vue'
import { ValidationObserver, ValidationProvider } from 'vee-validate'
import { required } from '@core/utils/validations/validations'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'

export default {
  name: 'EditCoupon',
  components: {
    ValidationProvider,
    ValidationObserver,
    BFormSelect,
    BRow,
    BCol,
    BForm,
    BFormGroup,
    BFormInput,
    BModal,
  },
  props: {
    obj: {
      type: Object,
      default: () => {
      },
    },
  },
  data() {
    return {
      required,

      dataId: '',
      dataFirstName: '',
      dataLastName: '',
      dataEmail: '',
      dataPhone: '',
      dataStartedAt: '',
      companyId: '',
    }
  },
  computed: {
    getCompanies() {
      /* eslint-disable no-param-reassign */
      return this.$store.state.company.companies
    },
  },
  watch: {
    obj() {
      const {
        id, first_name, last_name, email, phone, started_at, company_id,
      } = JSON.parse(JSON.stringify(this.obj))
      this.dataId = id
      // eslint-disable-next-line camelcase
      this.dataFirstName = first_name
      // eslint-disable-next-line camelcase
      this.dataLastName = last_name
      this.dataEmail = email
      this.dataPhone = phone
      // eslint-disable-next-line camelcase
      this.dataStartedAt = started_at
      // eslint-disable-next-line camelcase
      this.companyId = company_id
    },
  },
  created() {
    if (this.getCompanies.length === 0) {
      this.$store.dispatch('company/fetchName').then(res => {
        this.companies = res.data.data
      })
    }
  },
  methods: {
    update() {
      const obj = {
        id: this.dataId,
        employee_id: this.dataId,
        first_name: this.dataFirstName,
        last_name: this.dataLastName,
        email: this.dataEmail,
        phone: this.dataPhone,
        started_at: this.dataStartedAt,
        company_id: this.companyId,
      }
      this.$store.dispatch('employee/edit', obj).then(() => {
        this.$toast({
          component: ToastificationContent,
          props: {
            title: 'An employee has been updated successfully',
            icon: 'CheckCircleIcon',
            variant: 'success',
          },
        })
      })
    },
  },
}
</script>

<style scoped>

</style>
