<template>
  <div>
    <b-card
      ref="cardAction"
      title="All Employee"
    >
      <b-row>
        <b-col cols="12">
          <b-table
            id="my-table"
            :per-page="perPage"
            :current-page="currentPage"
            head-variant
            hover
            responsive
            :items="items"
            :fields="fields"
            bordered
          >
            <template #head()="data">
              <span
                class="text-primary"
                style="font-size:18px"
              >{{ data.label }}</span>
            </template>

            <template #table-busy>
              <div class="text-center text-dark my-2">
                <b-spinner class="align-middle" />
                <strong> Loading... </strong>
              </div>
            </template>

            <template #cell(id)="row">
              {{ row.item.id }}
            </template>

            <template #cell(name)="row">
              {{ row.item.name }}
            </template>

            <template #cell(percentage)="row">
              % {{ row.item.percentage }}
            </template>

            <template #cell(actions)="row">
              <b-button
                class="mr-1"
                size="sm"
                variant="primary"
                @click="edit(row.item)"
              > Edit </b-button>
              <b-button
                class="mt-1 mb-1"
                size="sm"
                variant="danger"
                @click="confirmDelete(row.item.id)"
              > Delete </b-button>
            </template>
          </b-table>
        </b-col>
      </b-row>
      <div class="mt-3">
        <b-pagination
          v-model="currentPage"
          :total-rows="rows"
          align="left"
          :per-page="perPage"
          aria-controls="my-table"
        />
      </div>
      <edit-coupon :obj="Obj" />
    </b-card>
  </div>
</template>

<script>
import EditCoupon from '@/views/admin/employees/EditEmployee.vue'
import {
  BRow, BCol, BTable, BButton, BPagination, BCard,
} from 'bootstrap-vue'
import { faEye, faTrash, faPlus } from '@fortawesome/free-solid-svg-icons'
import { library } from '@fortawesome/fontawesome-svg-core'

library.add(faEye, faTrash, faPlus)

export default {
  name: 'AllEmployees',
  components: {
    EditCoupon,
    BCard,
    BRow,
    BCol,
    BTable,
    BButton,
    BPagination,
  },
  data() {
    return {
      Obj: {},
      perPage: 10,
      currentPage: 1,
      add: true,
      busy: true,
      file: null,
      fields: [
        { key: 'id', label: 'id' },
        { key: 'first_name', label: 'first name' },
        { key: 'last_name', label: 'last name' },
        { key: 'email', label: 'email' },
        { key: 'phone', label: 'phone' },
        { key: 'started_at', label: 'started at' },
        { key: 'actions', label: 'action' },
      ],
      items: [],
    }
  },
  computed: {
    rows() {
      return this.getEmployees.length
    },
    getEmployees() {
      /* eslint-disable no-param-reassign */
      return this.$store.state.employee.employees
    },
  },
  created() {
    this.$store.dispatch('employee/fetch').then(res => {
      // console.log(res)
      this.items = res.data.data.data
    })
  },
  methods: {
    edit(data) {
      this.Obj = data
      // console.log(this.Obj)
      this.$bvModal.show('EditCat')
    },
    confirmDelete(id) {
      this.$swal({
        title: 'Delete Employee',
        text: 'Do you need delete employee',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Delete !',
        cancelButtonText: 'Cancel',
        customClass: {
          confirmButton: 'btn btn-primary',
          cancelButton: 'btn btn-outline-danger ml-1',
        },
        buttonsStyling: false,
      }).then(result => {
        if (result.value) {
          this.$swal({
            icon: 'success',
            title: 'Delete Employee',
            text: 'the Employee has been deleted',
            customClass: {
              confirmButton: 'btn btn-success',
            },
          })
          this.$store.dispatch('employee/delete', id).then(() => {
          })
        }
      })
    },
  },
}
</script>

